import React, { useCallback, useState } from 'react';
import propTypes from 'prop-types';
import MyContext from './MyContext';
import { fetchIngredients, fetchDrinksIngredients } from '../service/getIngredients';

function MyProvider({ children }) {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [inputSearch, setinputSearch] = useState('');
  const [radio, setRadio] = useState('');
  const [urlFoods, setUrlFoods] = useState({ ingredient: 'https://www.themealdb.com/api/json/v1/1/filter.php?i=', name: 'https://www.themealdb.com/api/json/v1/1/search.php?s=', firstLetter: 'https://www.themealdb.com/api/json/v1/1/search.php?f=' });
  const [urlDrinks, setUrlDrinks] = useState({ ingredient: 'https://www.thecocktaildb.com/api/json/v1/1/filter.php?i=', name: 'https://www.thecocktaildb.com/api/json/v1/1/search.php?s=', firstLetter: 'https://www.thecocktaildb.com/api/json/v1/1/search.php?f=' });
  const [recipes, setRecipes] = useState({ meals: [], drinks: [] });
  const [originalRecipes, setOriginalRecipes] = useState({ meals: [], drinks: [] });
  const [showSearch, setShowSearch] = useState(false);
  const [searched, setSearched] = useState(false);
  const [categories, setCategories] = useState([]);
  const [recipesByIng, setRecipesByIng] = useState([]);
  const [details, setDetails] = useState({});
  const [arrayIngredients, setArrayIngredients] = useState({});
  const [recommend, setRecommend] = useState({});
  const [favorite, setFavorite] = useState([]);
  const [filterFavorite, setfilterFavorite] = useState([]);
  const [doneRecipes, setdoneRecipes] = useState([]);

  const handleRadio = ({ target }) => {
    setRadio(target.value);
  };

  const handleInputSearch = ({ target }) => {
    setinputSearch(target.value);
  };

  const handleInputEmail = ({ target }) => {
    setEmail(target.value);
  };

  const handleInputPassword = ({ target }) => {
    setPassword(target.value);
  };

  const getAllRecipes = async (type) => {
    const url = (type === 'meals' ? (
      'https://www.themealdb.com/api/json/v1/1/search.php?s='
    ) : (
      'https://www.thecocktaildb.com/api/json/v1/1/search.php?s='
    ));
    const request = await fetch(url);
    const data = await request.json();
    setOriginalRecipes(data);
    setRecipes(data);
    return data;
  };

  const getApiFood = async (url, type) => {
    const request = await fetch(url);
    const data = await request.json();
    if (data[type] === null) {
      return global.alert('Sorry, we haven\'t found any recipes for these filters.');
    }
    setRecipes(data);
    return data;
  };

  const getApiDetails = useCallback(async (url, type) => {
    const request = await fetch(url);
    const data = await request.json();
    setDetails(data);
    const array = Object.entries(data[type][0]);
    const filterIngredients = array.filter(
      (elem) => (elem[0].includes('strIngredient') && !(
        elem[1] === '' || elem[1] === null)),
    );
    const filterMens = array.filter(
      (elem) => (elem[0].includes('strMeasure') && !(
        elem[1] === '' || elem[1] === null)),
    );
    setArrayIngredients({ filterIngredients, filterMens });
  }, []);

  const getCategories = async (type) => {
    const url = (type === 'meals' ? (
      'https://www.themealdb.com/api/json/v1/1/list.php?c=list'
    ) : (
      'https://www.thecocktaildb.com/api/json/v1/1/list.php?c=list'
    ));
    const request = await fetch(url);
    const data = await request.json();
    setCategories(data);
    return data;
  };

  const getRecipesByCategory = async (categoryName, type) => {
    const rootUrl = (type === 'meals' ? (
      'https://www.themealdb.com/api/json/v1/1/filter.php?c='
    ) : (
      'https://www.thecocktaildb.com/api/json/v1/1/filter.php?c='
    ));
    const endpoint = `${rootUrl}${categoryName}`;
    const request = await fetch(endpoint);
    const data = await request.json();
    setRecipes(data);
    return data;
  };

  const exploreRandom = async (url, type) => {
    const result = await getApiFood(url, type);
    console.log(result);
    return result;
  };

  const selectApi = (objUrl, type) => {
    let url = objUrl;
    switch (radio) {
    case 'ingredient': url = `${objUrl.ingredient}${inputSearch}`;
      break;
    case 'name': url = `${objUrl.name}${inputSearch}`;
      break;
    case 'first-letter':
      if (inputSearch.length === 1) {
        url = `${objUrl.firstLetter}${inputSearch}`;
      } else {
        return global.alert('Your search must have only 1 (one) character');
      }
      break;
    default: {
      console.log('radio not found');
    }
    }
    return getApiFood(url, type);
  };

  const getApiRecomend = useCallback(async (url, type) => {
    const result = await fetch(url);
    const data = await result.json();
    const sixLength = 6;
    const six = data[type].filter((_elem, i) => i < sixLength);
    setRecommend(six);
    return data;
  }, []);

  const unfavoriteRecipe = (id = details.meals[0].idMeal) => {
    const favoriteRecipes = JSON.parse(localStorage.getItem('favoriteRecipes'));
    const newFavorites = favoriteRecipes.filter((element) => (
      element.id !== id
    ));
    setFavorite(newFavorites);
    localStorage.setItem('favoriteRecipes', JSON.stringify(newFavorites));
  };

  const funcFavorite = () => JSON.parse(localStorage.getItem('favoriteRecipes'));

  const filter = (recipeType) => {
    if (recipeType === undefined) {
      console.log('entrou');
      return funcFavorite();
    }
    const arrayFilter = funcFavorite().filter((elem) => elem.type === recipeType);
    setfilterFavorite(arrayFilter);
  };

  const context = {
    email,
    password,
    handleInputEmail,
    handleInputPassword,
    inputSearch,
    radio,
    handleInputSearch,
    handleRadio,
    selectApi,
    urlFoods,
    setUrlFoods,
    urlDrinks,
    setUrlDrinks,
    recipes,
    showSearch,
    setShowSearch,
    categories,
    setCategories,
    getCategories,
    setRecipes,
    getRecipesByCategory,
    getAllRecipes,
    searched,
    setSearched,
    exploreRandom,
    fetchIngredients,
    fetchDrinksIngredients,
    recipesByIng,
    setRecipesByIng,
    getApiDetails,
    details,
    arrayIngredients,
    getApiRecomend,
    recommend,
    originalRecipes,
    setOriginalRecipes,
    favorite,
    setFavorite,
    setDetails,
    unfavoriteRecipe,
    filter,
    filterFavorite,
    doneRecipes,
    setdoneRecipes,
  };
  return (
    <MyContext.Provider value={ context }>
      {children}
    </MyContext.Provider>
  );
}

MyProvider.propTypes = {
  children: propTypes.objectOf,
}.isRequired;

export default MyProvider;
